<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      <vs-button class="p-3 mr-4" v-if="receipt.receiptId" color="primary" type="filled" :href="receiptDownloadUrl" icon-pack="far" icon="fa-receipt">{{ $t('RECEIPT') }}</vs-button>
    </div>
    <div id="loading-container" class="nv-row row hover vs-con-loading__container">
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row" v-if="deposit.title">
          <span>{{ $t('DEPOSIT_TITLE') }}:</span>
          <span>{{ deposit.title | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('DEPOSIT_NUMBER') }}:</span>
          <span>{{ deposit.accountNumber }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('DEPOSIT_TYPE') }}:</span>
          <span>{{ deposit.typeName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('START_DATE') }}:</span>
          <span>{{ deposit.startDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('MATURITY_DATE') }}:</span>
          <span>{{ deposit.maturityDate | formatUnixDate }}</span>
        </p>
        <p class="info-row" v-if="deposit.settlementAccountNumber">
          <span>{{ $t('SETTLEMENT_ACCOUNT_NUMBER') }}:</span>
          <span>{{ deposit.settlementAccountNumber | placeholder('-') }}</span>
        </p>
      </div>

      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row" v-if="deposit.currencyAbbreviation">
          <span>{{ $t('CURRENCY') }}:</span>
          <span>{{ deposit.currencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('DEPOSIT_AMOUNT') }}:</span>
          <span>{{ deposit.depositAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="deposit.maturityAmount">
          <span>{{ $t('MATURITY_AMOUNT') }}:</span>
          <span>{{ deposit.maturityAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('INTEREST_RATE') }}:</span>
          <span>{{ deposit.interestRate | placeholder('-') }}%</span>
        </p>
        <p class="info-row">
          <span>{{ $t('INTEREST_AMOUNT') }}:</span>
          <span>{{ deposit.interestAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="deposit.interestPaymentTermDescription">
          <span>{{ $t('INTEREST_PAYMENT_TERM') }}:</span>
          <span>{{ deposit.interestPaymentTermDescription | placeholder('-') }}</span>
        </p>
      </div>
    </div>
  </nv-card>
</template>

<script>
import TermDepositService from '@/services/term-deposit/TermDepositService'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import AlertMixin from '@/mixins/commons/AlertMixin'

export default {
  name: 'term-deposit-view',
  mixins: [NumberStyleMixin, LoadingMixin, AlertMixin],
  data: function () {
    return {
      deposit: {},
      receipt: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      await TermDepositService.termDeposits({
        accountNumber: this.$route.params.id
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.deposit = response.data.accountList[0] || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })

      TermDepositService.termDepositReceipt({
        accountNumber: this.$route.params.id
      }).then(function (response) {
        self.receipt = response.data.receiptList[0] || {}
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  computed: {
    receiptDownloadUrl: function () {
      let baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : ''
      let appId = this.$store.getters.appId
      let customerId = this.$store.getters.activeExternalId.externalId
      return `${baseUrl}/apps/Connect/DownloadSecure/downloadTermDepositReceipt?sourceAppId=${appId}&customerId=${customerId}&accountNumber=${this.deposit.accountNumber}&receiptId=${this.receipt.receiptId}`
    }
  },
  mounted () {
    this.load()
  }
}
</script>
